import React from 'react';
import PT from 'prop-types';
import { MdClear } from 'react-icons/md';
import Layout from '../../components/layout';
import MoneyPrediction from '../../components/calculators/MoneyPrediction';
import SEO from '../../components/seo';
import Box from '../../components/cardbox';
import Input from '../../components/Input';
import styles from './money_prediction.module.scss';
import Button from '../../components/Button';

const ExpectedExpense = ({
  onDelete, id, year = '', expense, onChange,
}) => (
  <div className={styles.expected_expense}>
    <span>In</span>
    <Input
      onChange={e => onChange(id, e.target.value, expense)}
      value={year}
      placeholder="Year"
    />
    <span> I am going to spend </span>
    <Input
      onChange={e => onChange(id, year, e.target.value)}
      value={expense}
      placeholder="Expense"
    />
    {' '}
    <span className={styles.clear_span} role="presentation" onClick={onDelete}>
      <MdClear />
    </span>
  </div>
);

ExpectedExpense.propTypes = {
  onDelete: PT.func.isRequired,
  id: PT.number.isRequired,
  year: PT.oneOfType([PT.string, PT.number]).isRequired,
  expense: PT.oneOfType([PT.string, PT.number]).isRequired,
  onChange: PT.func.isRequired,
};

class MoneyPredictionPage extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      monthlySalary: 1000,
      monthlyExpenses: 700,
      startingPoint: 2000,
      expInputs: [],
      predictForYears: 10,
      error: {},
      expectedExpenses: {},
    };
  }

  createArrayOfInputs = () => {
    const { expInputs, expectedExpenses } = this.state;
    return expInputs.map((i) => {
      const onChangeValue = (e) => {
        this.setState({
          expectedExpenses: {
            ...expectedExpenses,
            [i]: {
              year: (expectedExpenses[i] || {}).year || '',
              value: e.target.value,
            },
          },
        });
      };
      const onChangeYear = (e) => {
        this.setState({
          expectedExpenses: {
            ...expectedExpenses,
            [i]: {
              year: e.target.value,
              value: (expectedExpenses[i] || {}).value || 0,
            },
          },
        });
      };

      return (
        <div className={styles.expected_expense} key={i}>
          <span>In</span>
          <Input
            onChange={onChangeYear}
            value={(expectedExpenses[i] || {}).year || ''}
            placeholder="Year"
          />
          <span> I am going to spend </span>
          <Input
            onChange={onChangeValue}
            value={(expectedExpenses[i] || {}).value || ''}
            placeholder="Expense"
          />
          {' '}
          <span className={styles.clear_span} role="presentation" onClick={() => this.removeExpInput(i)}>
            <MdClear />
          </span>
        </div>
      );
    });
  };

  changeMonthlySalary = e => this.setValueWithCheck('monthlySalary', e);

  changeMonthlyExpenses = e => this.setValueWithCheck('monthlyExpenses', e);

  changePredictYears = e => this.setValueWithCheck('predictForYears', e);

  changeStartingPoint = e => this.setValueWithCheck('startingPoint', e);

  addExpInput = () => {
    const { expectedExpenses } = this.state;
    const id = `
      ${Math.ceil(Math.random() * 100000)}
      ${Math.ceil(Math.random() * 100000)}
    `;

    this.setState({
      expectedExpenses: {
        ...expectedExpenses,
        [id]: {
          id,
          expense: 0,
          year: 0,
        },
      },
    });
  };

  removeExpInput = (num) => {
    const { expectedExpenses, expInputs } = this.state;
    const newExpectedExpenses = {
      ...expectedExpenses,
    };
    delete newExpectedExpenses[num];
    this.setState({
      expInputs: expInputs.filter(item => item !== num),
      expectedExpenses: newExpectedExpenses,
    });
  };

  setValueWithCheck = (key, e) => {
    const { error } = this.state;
    const conversion = parseInt(e.target.value, 10);
    if (Number.isNaN(conversion)) {
      this.setState({ error: { ...error, [key]: true }, [key]: e.target.value });
    } else {
      const copy = { ...error };
      delete copy[key];
      this.setState({ error: copy, [key]: conversion });
    }
  }

  changeExpectedInputHandler = (id, year, expense) => {
    const { expectedExpenses } = this.state;
    this.setState({
      expectedExpenses: {
        ...expectedExpenses,
        [id]: {
          id,
          year,
          expense,
        },
      },
    });
  }

  deleteExpectedInputHandler = (id) => {
    const { expectedExpense } = this.state;
    const newObj = { ...expectedExpense };
    delete newObj[id];
    this.setState({ expectedExpenses: newObj });
  }

  render() {
    const {
      startingPoint, monthlySalary, predictForYears, monthlyExpenses, error, expectedExpenses,
    } = this.state;

    return (
      <Layout withFooter={false}>
        <SEO
          title="Future balance calculator."
          description="Struggling with money? Unclear future? Let's compute your future money balance. Add your income, expenses, expected expenses and let's look what the future balance will look like."
          keywords={['money', 'calculator', 'expenses', 'income', 'future']}
        />
        <Box>
          <div style={{ paddingTop: 20 }}>
            <div className={styles.input_grid}>
              <div>
                How much money do you actually have? (This is starting point)
              </div>
              <Input value={startingPoint} onChange={this.changeStartingPoint} />
            </div>

            <div className={styles.input_grid}>
              <div>Expected monthly salary (after tax)</div>
              <Input value={monthlySalary} onChange={this.changeMonthlySalary} />
            </div>

            <div className={styles.input_grid}>
              <div>Your monthly expenses ?</div>
              <Input value={monthlyExpenses} onChange={this.changeMonthlyExpenses} />
            </div>

            <div className={styles.input_grid}>
              <div>For how much years you want prediction to compute ?</div>
              <Input value={predictForYears} onChange={this.changePredictYears} />
            </div>

            <div className={styles.input_grid}>
              <div>Do you have expected expenses at some year?</div>
            </div>

            {Object.entries(expectedExpenses).map(([id, obj]) => (
              <ExpectedExpense
                id={id}
                year={obj.year}
                expense={obj.expense}
                onDelete={this.deleteExpectedInputHandler}
                onChange={this.changeExpectedInputHandler}
              />
            ))}
            <Button type="button" onClick={this.addExpInput}>Add expense</Button>
            <div style={{ paddingTop: 20 }}>
              {
                Object.keys(error).length === 0 && (
                  <MoneyPrediction
                    monthSalary={monthlySalary}
                    monthExpenses={monthlyExpenses}
                    startingPoint={startingPoint}
                    time={predictForYears}
                    theoreticalExpenses={Object.values(expectedExpenses).reduce((acc, val) => {
                      acc[val.year] = acc[val.year] === undefined
                        ? +val.expense : (+acc[val.year] + (+val.expense));
                      return acc;
                    }, {})}
                  />
                )
              }
            </div>
          </div>
        </Box>
      </Layout>
    );
  }
}

export default MoneyPredictionPage;
