import React from 'react';
import PropTypes from 'prop-types';
import {
  LineChart, XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Line,
  Legend, ResponsiveContainer,
} from 'recharts';

const chartMargin = {
  top: 5,
  right: 30,
  left: 20,
  bottom: 5,
};

class MoneyPrediction extends React.PureComponent {
  getPredictedData = (year, n = 10) => {
    const {
      startingPoint, monthSalary, monthExpenses, theoreticalExpenses,
    } = this.props;
    const preds = [{ year, balance: startingPoint }];

    for (let i = year + 1; i < year + n + 1; i += 1) {
      let { balance } = preds[preds.length - 1];
      balance += monthSalary * 12;
      balance -= monthExpenses * 12;

      if (theoreticalExpenses) {
        if (i.toString() in theoreticalExpenses) {
          balance -= theoreticalExpenses[i];
        }
      }

      preds.push({
        year: i,
        balance,
      });
    }

    return preds;
  };

  render() {
    const { time } = this.props;
    const nowDate = new Date();
    const nowYear = nowDate.getFullYear();
    const predictions = this.getPredictedData(nowYear, time);

    return (
      <ResponsiveContainer width="100%" height={400}>
        <LineChart data={predictions} margin={chartMargin}>
          <XAxis dataKey="year" />
          <YAxis domain={[0, 'auto']} />
          <CartesianGrid strokeDasharray="3 3" />
          <Tooltip />
          <Legend />
          <Line type="monotone" dataKey="balance" stroke="#8884d8" />
        </LineChart>
      </ResponsiveContainer>
    );
  }
}

MoneyPrediction.propTypes = {
  monthSalary: PropTypes.number.isRequired,
  monthExpenses: PropTypes.number,
  theoreticalExpenses(props, propName, componentName) {
    for (let keys = Object.keys(), key = null; key < keys.length; key += 1) {
      if (!keys[key].match(/^[0-9]{4}$/)) {
        return new Error(`${componentName} = Keys should be year`);
      }
    }
    return true;
  },
  startingPoint: PropTypes.number,
  time: PropTypes.number,
};

MoneyPrediction.defaultProps = {
  monthExpenses: 0,
  theoreticalExpenses: 0,
  startingPoint: 0,
  time: 15,
};

export default MoneyPrediction;
